<template>
    <div>
        <Movable 
            :id="id"
            :size="size"
            :styles="styles"
            :pos="pos"
            :selected="isSelected"
            @object-selected="objectSelection"
            @deleteElement="deleteElement"
        >
            <slot></slot>
        </Movable>
        <div v-if="id == selectedObjectId">
            <Teleport to="#optionsMenu">
                {{ selectedObjectId }}
                <BlockMenu
                    :size="size"
                    :styles="styles"
                    :pos="pos"
                    @deleteElement="deleteElement"
                    @changeSize="changeSize"
                    @changeStyle="changeStyle"
                    @changePos="changePos"
                ></BlockMenu>
            </Teleport>
        </div>
    </div>
</template>

<script>
import Movable from '@/components/Movable/Movable.vue'
import BlockMenu from '@/components/Movable/BlockMenu.vue'

export default {
    components: {
        Movable,
        BlockMenu,
    },
    props: {
        id: {
            type: Number
        },
        selectedObjectId: {
            type: Number
        },
        innerSelection: {
            type: Boolean
        }
    },

    data: () => ({
        size: {
            width: 100,
            height: 100,
        },
        styles: {
            fontSize: '12px',
            color: 'black',
            backgroundColor: 'white',
            fontFamily: 'Impact',
        },
        pos: {
            x: 200,
            y: 200,
            rotation: 0
        },
        isSelected: false,
    }),

    mounted () {
        this.$emit('changeStyles', this.styles)
        this.$emit('changeSize', this.size)
    },

    methods: {
        objectSelection () {
            this.$emit('objectSelection', this.id);
        },
        changeStyle (data) {
            this.styles[Object.keys(data)[0]] = data[Object.keys(data)[0]]
            this.$emit('changeStyles', this.styles)
        },
        changeSize (data) {
            this.size[Object.keys(data)[0]] = data[Object.keys(data)[0]]
            this.$emit('changeSize', this.size)
        },
        changePos (data) {
            this.pos[Object.keys(data)[0]] = data[Object.keys(data)[0]]
        },
        deleteElement () {
            this.$emit('deleteElement', this.id)
        },
    },

    watch: {
        innerSelection (newValue) {
            this.isSelected = newValue
        },
        isSelected (newValue) {
            if (newValue) {
                this.objectSelection()
            }
        }
    },
}
</script>

